.solution{
	margin-top: 100rem;
	@media screen and (max-width: 767px) {
		margin-top: 66rem;
	}
	@include block-title(675rem, 675rem);
	&__accordion{
		counter-reset: listCounter;
		margin-top: 54rem;
		&-item{
			padding: 0 50rem;
			border-radius: 10rem;
			border: solid 1px #e0e0e0;
			box-shadow: 0 5rem 40rem 0 rgba(0, 0, 0, 0.05);
			background-color: var(--light-color);
			counter-increment: listCounter;
			list-style: none;
			@media screen and (max-width: 767px) {
				padding: 13rem 29rem;
			}
			&:nth-child(n+2){
				margin-top: 20rem;
			}
		}
		&-title{
			position: relative;
			padding-left: 52rem;
			@media screen and (max-width: 767px) {
				padding-left: 40rem;
			}
			&::before{
				content: counter(listCounter);
				width: 32rem;
				height: 32rem;
				text-align: center;
				font-size: 22rem;
				font-weight: 700;
				color: var(--light-color);
				padding-top: 2rem;
				border-radius: 50%;
				background-color: #232d60;
				display: inline-block;
				position: absolute;
				top: 50%;
				left: 0;
				transform: translateY(-50%);
				@media screen and (max-width: 767px) {
					width: 25rem;
					height: 26rem;
					font-size: 16.5rem;
				}
			}
		}
		&-heading{
			display: flex;
			align-items: center;
			font-size: 22rem;
			letter-spacing: 0.44rem;
			height: 62rem;
			@media screen and (max-width: 767px) {
				font-size: 18rem;
				font-weight: 500;
				letter-spacing: 0.36rem;
				height: auto;
			}
		}
		&-body{
			font-size: 16rem;
			letter-spacing: 0.32rem;
			display: none;
			border-top: 1px solid rgba(#000, 0.1);
			padding: 32rem 54rem 43rem;
		}
	}
	&__tile{
		position: relative;
		display: flex;
		border-radius: 10rem;
		box-shadow: 0 5rem 40rem 0 rgba(0, 0, 0, 0.05);
		background-color: #232d60;
		margin-top: 21rem;
		padding: 43rem 50rem 56rem 96rem;
		overflow: hidden;
		@media screen and (max-width: 991px) {
			flex-wrap: wrap;
			margin-top: 18rem;
			padding: 33rem 40rem 35rem;
		}
		&::before{
			content: '';
			display: inline-block;
			position: absolute;
			top: 13rem;
			right: -174rem;
			width: 417rem;
			height: 297rem;
			background: url('../img/akom.png') no-repeat center / contain;
			@media screen and (max-width: 767px) {
				top: 150rem;
				right: -370rem;
			}
		}
		&::after{
			content: '';
			display: inline-block;
			position: absolute;
			top: 22rem;
			right: -262rem;
			width: 813rem;
			height: 335rem;
			background: url('../img/solution_pattern.png') no-repeat center / contain;
			@media screen and (max-width: 767px) {
				top: auto;
				right: auto;
				left: 200rem;
				bottom: 60rem;
			}
		}
		&-title {
			font-size: 28rem;
			letter-spacing: 0.84rem;
			color: #fffffe;
			max-width: 370rem;
			z-index: 2;
			@media screen and (max-width: 991px) {
				font-size: 19rem;
				font-weight: 700;
				letter-spacing: 0.4rem;
				width: 100%;
			}
			&::before{
				content: '';
				display: inline-block;
				position: absolute;
				bottom: -145rem;
				left: 50rem;
				width: 362rem;
				height: 246rem;
				background: url('../img/solution_document.png') no-repeat center / contain;
				@media screen and (max-width: 991px) {
					display: none;
				}
			}
		}
		&-text {
			position: relative;
			font-size: 18rem;
			line-height: 1.44;
			letter-spacing: 0.36rem;
			color: #fffffe;
			max-width: 383rem;
			margin: 5rem 0 0 72rem;
			@media screen and (max-width: 991px) {
				font-size: 15rem;
				line-height: 1.47;
				letter-spacing: 0.3rem;
				width: 100%;
				margin: 22rem 0 0;
			}
			span{
				font-weight: 700;
				color: #ffd01c;
			}
		}
	}
}