.questions {
	margin-top: 100rem;
	@media screen and (max-width: 767px) {
		margin-top: 66rem;
	}
	@include block-title(675rem, 675rem);
	&__row {
		display: flex;
		justify-content: space-between;
		margin-top: 55rem;
		@media screen and (max-width: 991px) {
			flex-wrap: wrap;
			justify-content: center;
			margin-top: 48rem;
		}
	}
	&__accordion{
		flex: 0 0 745rem;
		max-width: 100%;
		@media screen and (max-width: 991px) {
			flex: 0 0 420rem;
		}
		&-item{
			padding: 0 50rem;
			border-radius: 10rem;
			border: solid 1px #e0e0e0;
			box-shadow: 0 5rem 40rem 0 rgba(0, 0, 0, 0.05);
			background-color: var(--light-color);
			@media screen and (max-width: 767px) {
				padding: 13rem 29rem;
			}
			&:nth-child(n+2){
				margin-top: 20rem;
			}
		}
		&-title{
			position: relative;
			padding-left: 31rem;
			&::before{
				content: '?';
				font-size: 22rem;
				font-weight: 700;
				letter-spacing: 0.44rem;
				color: rgba(0, 0, 0, 0.3);
				display: inline-block;
				position: absolute;
				top: 50%;
				left: 0;
				transform: translateY(-15rem);
			}
		}
		&-heading{
			display: flex;
			align-items: center;
			font-size: 18rem;
			letter-spacing: 0.36rem;
			height: 62rem;
			@media screen and (max-width: 767px) {
				height: auto;
			}
		}
		&-body{
			font-size: 16rem;
			letter-spacing: 0.32rem;
			display: none;
			border-top: 1px solid rgba(#000, 0.1);
			color: var(--gray-color);
			padding: 28rem 43rem 38rem;
		}
	}
	&__form {
		margin-top: 25rem;
		&-wrapper {
			flex: 0 0 420rem;
			max-width: 100%;
			padding: 42rem 52rem 40rem 48rem;
			border-radius: 10rem;
			box-shadow: 0 5rem 40rem 0 rgba(0, 0, 0, 0.05);
			background-color: var(--light-color);
			text-align: center;
			@media screen and (max-width: 991px) {
				margin-top: 34rem;
			}
			@media screen and (max-width: 767px) {
				padding: 42rem 30rem 40rem;
			}
		}
		&-title {
			font-size: 22rem;
			font-weight: 700;
			letter-spacing: 0.44rem;
			max-width: 313rem;
			margin: 0 auto;
		}
		&-subtitle {
			font-size: 16rem;
			letter-spacing: 0.32rem;
			max-width: 313rem;
			margin: 15rem auto 0;
		}
	}
	&__field {
		padding: 34rem 40rem 33rem 67rem;
		border-radius: 5rem;
		box-shadow: inset 0 0 15px 0 rgba(0, 0, 0, 0.05);
		background-color: #f7f7f7;
		font-size: 18rem;
		letter-spacing: 0.36rem;
		color: rgb(1, 1, 1);
		width: 100%;
		@media screen and (max-width: 767px) {
			padding: 34rem 26rem 32rem 56rem;
		}
		&::placeholder{
			color: rgba(1, 1, 1, 0.5);
		}
		&-wrapper{
			position: relative;
			&::before{
				content: '';
				display: inline-block;
				position: absolute;
				top: 50%;
				transform: translateY(-50%);
				left: 40rem;
				width: 16rem;
				height: 16rem;
				@media screen and (max-width: 767px) {
					left: 30rem;
				}
			}
			&:nth-child(1){
				&::before{
					background: url('../img/user.png') no-repeat center / contain;
				}
			}
			&:nth-child(2){
				&::before{
					background: url('../img/callback_1.png') no-repeat center / contain;
				}
			}
			&:nth-child(n+2){
				margin-top: 15rem;
			}
		}
	}
	&__button {
		margin-top: 15rem;
		width: 100%;
		font-size: 16rem;
		letter-spacing: 0.32rem;
		color: var(--dark-color);
		text-align: center;
		padding: 34rem 48rem 32rem;
		border-radius: 5rem;
		box-shadow: 0px 5rem 0 0 #e3bd2b;
		background-image: linear-gradient(to top, #f3c92a, #f4db7d);
		span {
			font-size: 22rem;
			font-weight: bold;
			letter-spacing: 0.44rem;
			color: #010101;
			display: block;
		}
		@media screen and (max-width: 991px) {
			padding: 33rem 5rem 31rem;
		}
	}
	&__confidentiality {
		font-size: 12rem;
		letter-spacing: 0.24rem;
		color: var(--gray-color);
		max-width: 210rem;
		margin: 22rem auto 0;
		&-link{
			color: var(--gray-color);
			text-decoration: underline;
		}
	}
}