//     sass- -  root
// colors
:root {
  // base
  --font-family: "Exo2", sans-serif;
  --content-width: 1200rem;
  --container-offset: 100rem;
  --container-width: calc(var(--content-width) + (var(--container-offset) * 2));

  // breakpoints
  --tablet: 991px;
  --mobile: 767px;

  // colors
  --dark-color: #000;
  --gray-color: rgba(0, 0, 0, 0.6);
  --light-color: #fff;
}
