@mixin button($padding, $margin-bottom) {
  font-size: 16rem;
  letter-spacing: 0.32rem;
  color: var(--dark-color);
  text-align: center;
  padding: $padding;
  border-radius: 5rem;
  box-shadow: 0px 5rem 0 0 #e3bd2b;
  background-image: linear-gradient(to top, #f3c92a, #f4db7d);
  span {
    font-size: 22rem;
    font-weight: bold;
    letter-spacing: 0.44rem;
    color: #010101;
    display: block;
    margin-bottom: $margin-bottom;
  }
}