// 
.header{
	&__container{
		display: flex;
		justify-content: space-between;
		align-items: center;
		padding-top: 30rem;
		@media screen and (max-width: 991px) and (min-width: 768px)  {
			flex-wrap: wrap;
			justify-content: center;
		}
	}
	&__company {
		display: flex;
		align-items: center;
		@media screen and (max-width: 991px) and (min-width: 768px) {
			margin-left: 15rem;
		}
	}
	&__logo {
		width: 100rem;
		@media screen and (max-width: 991px) {
			width: 80rem;
		}
	}
	&__description {
		letter-spacing: 0.32rem;
		color: var(--gray-color);
		max-width: 256rem;
		margin-left: 22rem;
		@media screen and (max-width: 991px) {
			margin-left: 14rem;
			max-width: 200rem;
			font-size: 12rem;
		}
		@media screen and (max-width: 767px) {
			display: none;
		}
	}
	&__messengers {
		display: flex;
		align-items: center;
		@media screen and (max-width: 991px) {
			margin-left: 20rem;
			margin-right: 15rem;
		}
		@media screen and (max-width: 767px) {
			display: none;
		}
		&-text {
			letter-spacing: 0.32rem;
			max-width: 84rem;
			@media screen and (max-width: 991px) {
				max-width: 70rem;
				font-size: 12rem;
			}
		}
		&-list {
			display: flex;
			align-items: center;
			margin-left: 20rem;
			@media screen and (max-width: 991px) {
				margin-left: 5rem;
			}
		}
		&-item {
			&:nth-child(n+2){
				margin-left: 10rem;
				@media screen and (max-width: 991px) {
					margin-left: 5rem;
				}
			}
		}
		&-image{
			height: auto;
			width: 32rem;
			@media screen and (max-width: 991px) {
				width: 24rem;
			}
		}
	}
	&__button {
		padding: 19rem 38rem 17rem;
		border-radius: 5rem;
		box-shadow: 0 3rem 0 0 #1b234e;
		color: var(--light-color);
		background-image: linear-gradient(to top, #232d60, #4857a6);
		@media screen and (max-width: 991px) {
			margin-top: 10rem;
			margin-right: 70rem;
		}
		@media screen and (max-width: 767px) {
			display: none;
		}
	}
	&__contacts {
		text-align: right;
		@media screen and (max-width: 991px) and (min-width: 768px) {
			margin-top: 5rem;
		}
		&-item{
			&:nth-child(1){
				a{
					letter-spacing: 0.32rem;
					color: var(--gray-color);
					@media screen and (max-width: 991px) {
						font-size: 16rem;
					}
				}
			}
			&:nth-child(2){
				margin-top: 9rem;
				@media screen and (max-width: 991px) {
					margin-top: 5rem;
				}
				a{
					font-size: 20rem;
					font-weight: bold;
					letter-spacing: 0.4rem;
					@media screen and (max-width: 991px) {
						font-size: 18rem;
						letter-spacing: 0.36rem;
					}
				}
			}
		}
	}
}
.burger {
	padding: 7rem;
	border-radius: 5rem;
	box-shadow: 0 5rem 20rem 0 rgba(0, 0, 0, 0.1);
	background-color: #232d60;
	width: 36rem;
	height: 32rem;
	span{
		display: block;
		width: 100%;
		height: 2rem;
		border-radius: 1rem;
		box-shadow: 0 10rem 16rem 4rem rgba(0, 0, 0, 0.1);
		background-color: #fff;
		&:nth-child(2){
			margin: 6rem 0;
		}
	}
	&-wrapper {
		display: none;
		@media screen and (max-width: 767px) {
			display: block;
		}
	}
}