.chat {
	position: relative;
	display: flex;
	justify-content: space-between;
	margin-top: 98rem;
	&::before{
		content: '';
		display: inline-block;
		position: absolute;
		top: 78rem;
		left: 746rem;
		width: 1149rem;
		height: 335rem;
		background: url('../img/pattern.png') no-repeat center / contain;
		@media screen and (max-width: 767px) {
			top: 126rem;
			right: 63rem;
		}
	}
	@media screen and (max-width: 991px) {
		margin-top: 69rem;
	}
	&__image-wrapper {
		margin-top: 4rem;
		position: relative;
		&::before{
			content: '';
			display: inline-block;
			position: absolute;
			top: 10rem;
			right: -227rem;
			width: 297rem;
			height: 452rem;
			background: url('../img/iphone_1.png') no-repeat center / contain;
			z-index: -1;
		}
	}
	&__column {
		flex: 0 0 476rem;
	}
	&__pretitle {
		position: relative;
		font-size: 32rem;
		letter-spacing: 0.64rem;
		text-transform: uppercase;
		&::before{
			content: '';
			display: inline-block;
			position: absolute;
			top: 17rem;
			left: -37rem;
			width: 20rem;
			height: 45rem;
			background: url('../img/chat_arrow.png') no-repeat center / contain;
		}
	}
	&__title {
		text-align: left;
		margin-top: 15rem;
	}
	&__support {
		display: flex;
		align-items: center;
		font-size: 22rem;
		font-weight: 700;
		letter-spacing: 0.44rem;
		position: relative;
		margin-top: 25rem;
		&::before{
			content: '';
			display: inline-block;
			width: 22rem;
			height: 22rem;
			margin-right: 14rem;
			background: url('../img/time.png') no-repeat center / contain;
		}
	}
	&__list {
		margin-top: 35rem;
		&-item {
			display: flex;
			align-items: center;
			.chat__list-text{
				max-width: 266rem;
			}
			&:nth-child(1){
				.chat__list-text{
					max-width: 186rem;
				}
			}
			&:nth-child(n+2){
				margin-top: 20rem;
			}
		}
		&-icon {
			width: 50rem;
			height: 50rem;
			display: flex;
			justify-content: center;
			align-items: center;
			background-color: #ffd01c;
			border-radius: 50%;
			margin-right: 21rem;
		}
		&-text {
			font-size: 16rem;
			letter-spacing: 0.32rem;
		}
	}
}
.room {
	position: relative;
	display: flex;
	justify-content: space-between;
	margin-top: 88rem;
	&::before{
		content: '';
		display: inline-block;
		position: absolute;
		top: 94rem;
		right: 947rem;
		width: 1149rem;
		height: 335rem;
		background: url('../img/pattern.png') no-repeat center / contain;
		@media screen and (max-width: 767px) {
			top: 126rem;
			right: 63rem;
		}
	}
	@media screen and (max-width: 991px) {
		margin-top: 67rem;
	}
	&__column {
		flex: 0 0 550rem;
	}
	&__title {
		text-align: left;
	}
	&__subtitle {
		font-size: 20rem;
		font-weight: 700;
		letter-spacing: 0.25rem;
		margin-top: 21rem;
	}
	&__warning {
		display: flex;
		align-items: center;
		margin-top: 32rem;
		&-column {
			display: flex;
			align-items: center;

		}
		&-text {
			font-size: 18rem;
			letter-spacing: 0.36rem;
			max-width: 251rem;
			margin-left: 16rem;
			&--gray {
				font-size: 14rem;
				letter-spacing: 0.28rem;
				color: var(--gray-color);
				max-width: 207rem;
				margin-left: 21rem;
			}
		}
	}
	&__tile {
		position: relative;
		flex: 0 0 600rem;
		border-radius: 10rem;
		box-shadow: 0 5rem 40rem 0 rgba(0, 0, 0, 0.05);
		background-color: #232d60;
		padding: 34rem 25rem 51rem 50rem;
		margin-top: 8rem;
		&::before{
			content: '';
			display: inline-block;
			position: absolute;
			top: 80rem;
			left: 140rem;
			width: 813rem;
			height: 335rem;
			background: url('../img/solution_pattern.png') no-repeat center / contain;
		}
		&::after{
			content: '';
			display: inline-block;
			position: absolute;
			top: 66rem;
			left: -174rem;
			width: 286rem;
			height: 88rem;
			background: url('../img/room_image.png') no-repeat center / contain;
			z-index: -1;
		}
		&-title {
			font-size: 22rem;
			font-weight: 700;
			letter-spacing: 0.2rem;
			color: #fffffe;
		}
	}
	&__list {
		margin-top: 22rem;
		margin-left: 2rem;
		&-item {
			display: flex;
			align-items: center;
			&:nth-child(n+2){
				margin-top: 19rem;
			}
			&:nth-child(3){
				.room__list-text{
					margin-left: 12rem;
				}
			}
		}
		&-text{
			font-size: 18rem;
			letter-spacing: 0.36rem;
			color: var(--light-color);
			margin-left: 19rem;
		}
	}
}