.page {
  height: 100%;
  font-family: var(--font-family, sans-serif);
  -webkit-text-size-adjust: 100%;
  scroll-behavior: smooth;
  font-size: 1px;
  overflow-x: hidden;
  &._lock{
    overflow: hidden;
  }
  @media only screen and (max-width: 1400px) {
    font-size: 0.07vw;
  }
  @media only screen and (max-width: 991px) {
    font-size: 1px;
  }
}

.page__body {
  color: var(--dark-color);
  font-weight: normal;
  overflow-x: hidden;
  position: relative;
  left: 0;
  right: 0;
  font-size: 16rem;
  line-height: normal;
  min-width: 320px;
  min-height: 100%;
  margin: 0;
  &._lock{
    max-height: 100%;
    overflow-y: hidden;
  }
}

.visually-hidden {
  position: absolute;
  overflow: hidden;
  margin: -1px;
  border: 0;
  padding: 0;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
}

.container {
  margin: 0 auto;
  padding: 0 var(--container-offset);
  max-width: var(--container-width);
  @media screen and (max-width: 991px) {
    max-width: 768rem;
    padding: 0 20rem;
  }
  @media screen and (max-width: 767px) {
    max-width: 360rem;
    padding: 0 10rem;
  }
}

//  
.title{
  font-size: 32rem;
  font-weight: bold;
  letter-spacing: 0.64rem;
  text-align: center;
  color: var(--dark-color);
  margin: 0 auto;
  @media screen and (max-width: 991px) {
    font-size: 28rem;
  }
  @media screen and (max-width: 767px) {
    font-size: 23rem;
    letter-spacing: 0.4rem;
  }
}
.subtitle{
  font-size: 20rem;
  letter-spacing: 0.4rem;
  text-align: center;
  color: var(--dark-color);
  margin: 25rem auto 0;
  @media screen and (max-width: 767px) {
    margin-top: 20rem;
  }
}