.swiper {
  width: 100%;
}
.reviews {
	@include block-title(890rem, 635rem);
	margin-top: 91rem;
	@media screen and (max-width: 767px) {
		margin-top: 70rem;
	}
	&__slider {
		margin-top: 55rem;
		overflow: visible;
		@media screen and (max-width: 767px) {
			overflow: hidden;
		}
		&-buttons {
			display: flex;
			justify-content: center;
			margin-top: 35rem;
			@media screen and (max-width: 767px) {
				margin-top: 20rem;
			}
		}
		&-prev {
			font-size: 18rem;
			font-weight: 700;
			letter-spacing: 0.36rem;
			color: rgba(1, 1, 1, 0.2);
			text-transform: uppercase;
			display: flex;
			align-items: center;
			padding: 18rem 32rem 14rem;
			border-radius: 3rem;
			box-shadow: 0 3rem 0 0 #e9e9e9;
			background-color: #f0f0f0;
			.reviews__slider-arrow{
				margin-right: 9rem;
			}
		}
		&-next {
			font-size: 18rem;
			font-weight: 700;
			letter-spacing: 0.36rem;
			color: #010101;
			text-transform: uppercase;
			display: flex;
			align-items: center;
			padding: 18rem 32rem 14rem 31rem;
			border-radius: 3rem;
			box-shadow: 0 3rem 0 0 #e3bd2b;
			background-image: linear-gradient(to top, #f3c92a, #f4db7d);
			margin-left: 20rem;
			.reviews__slider-arrow{
				margin-left: 9rem;
			}
			@media screen and (max-width: 767px) {
				margin-left: 10rem;
			}
		}
		.swiper-slide{
			opacity: 0.5;
			transition: 0.5s;
		}
		.swiper-slide-active, .swiper-slide-active + .swiper-slide, .swiper-slide-active + .swiper-slide + .swiper-slide{
			opacity: 1;
		}
		@media screen and (max-width: 991px) {
			.swiper-slide-active + .swiper-slide + .swiper-slide{
				opacity: 0.5;
			}
		}
		@media screen and (max-width: 767px) {
			.swiper-slide-active + .swiper-slide{
				opacity: 0.5;
			}
		}
	}
	&__image {
		object-fit: cover;
		width: 100%;
		height: 100%;
		position: absolute;
		top: 0;
		right: 0;
		bottom: 0;
		left: 0;
		&-wrapper{
			width: 100%;
			height: 300rem;
			position: relative;
		}
	}
	&__video-button {
		position: absolute;
		top: 40rem;
		right: 26rem;
		display: flex;
		flex-direction: column;
		align-items: center;
		&-play {
			display: flex;
			justify-content: center;
			align-items: center;
			width: 80rem;
			height: 80rem;
			position: relative;
			&::before{
				content: '';
				display: inline-block;
				width: 80rem;
				height: 80rem;
				border-radius: 50%;
				box-shadow: 0 0 30px 0 #e3bd2b;
				background-image: linear-gradient(to top, #f3c92a, #f4db7d);
				position: absolute;
				top: 50%;
				left: 50%;
				transform: translate(-50%, -50%);
			}
		}
		&-image {
			position: relative;
			margin-left: 2rem;
			width: 29rem;
			height: 32rem;
		}
		&-text {
			font-size: 16rem;
			letter-spacing: 0.32rem;
			color: var(--light-color);
			border-bottom: 1px solid var(--light-color);
			text-transform: uppercase;
			margin-top: 13rem;
		}
	}
	&__name {
		font-size: 20rem;
		font-weight: 700;
		letter-spacing: 0.4rem;
		text-align: left;
		color: var(--light-color);
		position: absolute;
		bottom: 27rem;
		left: 40rem;
		&::before{
			content: '';
			display: inline-block;
			width: 30rem;
			height: 2rem;
			position: absolute;
			left: -40rem;
			top: 50%;
			transform: translateY(-50%);
			background-color: #e3bd2b;
		}
	}
	&__item{
		border-radius: 10rem;
		box-shadow: 0rem 5rem 40rem 0 rgba(0, 0, 0, 0.05);
		background-color: #fff;
		overflow: hidden;
		&-content{
			padding: 27rem 40rem 37rem;
			@media screen and (max-width: 767px) {
				padding: 27rem 30rem 37rem;
			}
		}
		&-row {
			display: flex;
			justify-content: space-between;
			align-items: center;
		}
		&-geolocation {
			display: flex;
			align-items: center;
			font-size: 18rem;
			font-weight: 700;
			letter-spacing: 0.36rem;
			text-align: left;
			&-icon {
				margin-right: 8rem;
				width: 12rem;
				height: 18rem;
			}
		}
		&-times {
			font-size: 16rem;
			letter-spacing: 0.32rem;
		}
		&-text {
			font-size: 14rem;
			letter-spacing: 0.28rem;
			color: var(--gray-color);
			margin-top: 16rem;
		}
		&-numbers {
			display: flex;
			justify-content: space-between;
			align-items: center;
			position: relative;
			&:nth-child(n+2){
				margin-top: 9rem;
			}
			&-wrapper{
				margin-top: 18rem;
			}
			&-text {
				font-size: 16rem;
				letter-spacing: 0.32rem;
				background: var(--light-color);
				padding-right: 7rem;
			}
			&::before{
				content: '';
				display: inline-block;
				position: absolute;
				right: 0;
				bottom: 2rem;
				left: 0;
				width: 100%;
				height: 1px;
				opacity: 0.1;
				border-bottom: dashed 1px #000;
				z-index: -1;
			}
		}
		&-number {
			font-size: 16rem;
			letter-spacing: 0.32rem;
			background: var(--light-color);
			padding-left: 6rem;
		}
		&-result {
			.reviews__item-numbers-text{
				font-size: 16rem;
				font-weight: 700;
				letter-spacing: 0.32rem;
				color: #232d60;
			}
			.reviews__item-number{
				font-size: 16rem;
				font-weight: 700;
				letter-spacing: 0.32rem;
				color: #e3bd2b;
			}
		}
	}
}