// 
.statistics{
  @include block-title(847rem, 300rem);
  &__tile {
    position: relative;
    display: flex;
    justify-content: space-between;
    background: #232d60 url('../img/statistics-bg.jpg') no-repeat center / cover;
    border-radius: 10rem;
    box-shadow: 0 10rem 16rem 4rem rgba(0, 0, 0, 0.1);
    margin-top: 50rem;
    padding: 35rem 118rem 44rem 70rem;
    &-title {
      position: relative;
      font-size: 28rem;
      font-weight: 700;
      letter-spacing: 0.56rem;
      color: var(--light-color);
      max-width: 200rem;
    }
    &::before{
      content: '';
      display: block;
      position: absolute;
      bottom: 0;
      left: 241rem;
      width: 187rem;
      height: 178rem;
      background: url('../img/akoms.png') no-repeat center / contain;
    }
    @media screen and (max-width: 991px) {
      max-width: 340rem;
      flex-wrap: wrap;
      overflow: hidden;
      background: #232d60 url('../img/statistics-bg_1.jpg') no-repeat center / cover;
      margin: 54rem auto 0;
      padding: 44rem 55rem 70rem;
      &::before{
        width: 97rem;
        height: 239rem;
        background: url('../img/akoms_1.png') no-repeat center / contain;
        top: 74rem;
        right: 0;
        bottom: auto;
        left: auto;
      }
    }
    @media screen and (max-width: 767px) {
      padding-bottom: 111rem;
    }
    @media screen and (max-width: 360px) {
      padding: 44rem 40rem 111rem;
    }
  }
  &__list {
    position: relative;
    display: flex;
    justify-content: flex-end;
    margin-top: 11rem;
    @media screen and (max-width: 991px) {
      flex-wrap: wrap;
      justify-content: flex-start;
      margin-top: 44rem;
    }
    &-item {
      position: relative;
      &:nth-child(1){
        max-width: 185rem;
        @media screen and (max-width: 991px) {
          max-width: 100%;
          width: 100%;
          .statistics__text{
            max-width: 176rem;
          }
        }
        .statistics__additional{
          background-color: #299144;
          width: 160rem;
          padding: 11rem 20rem 11rem 16rem;
        }
      }
      &:nth-child(2){
        max-width: 224rem;
        margin-left: 55rem;
        @media screen and (max-width: 991px) {
          max-width: 100%;
          width: 100%;
          margin-left: 0;
          margin-top: 104rem;
          .statistics__text{
            max-width: 220rem;
          }
        }
        .statistics__additional{
          background-color: #b84444;
          width: 150rem;
          padding: 11rem 15rem 11rem 16rem;
        }
      }
      &:nth-child(3){
        max-width: 125rem;
        margin-left: 25rem;
        @media screen and (max-width: 991px) {
          max-width: 100%;
          width: 100%;
          margin-left: 0;
          margin-top: 90rem;
          .statistics__text{
            max-width: 130rem;
          }
        }
        .statistics__additional{
          background-color: #38a19b;
          width: 160rem;
          padding: 11rem 16rem;
        }
      }
    }
  }
  &__number {
    font-size: 24rem;
    font-weight: 700;
    letter-spacing: 0.48rem;
    color: #ffd01c;
  }
  &__text {
    font-size: 18rem;
    letter-spacing: 0.36rem;
    color: var(--light-color);
    margin-top: 8rem;
  }
  &__additional {
    font-size: 13rem;
    letter-spacing: 0.26rem;
    color: var(--light-color);
    position: absolute;
    left: 13rem;
    bottom: -56rem;
    height: 50rem;
    display: flex;
    align-items: center;
    border-radius: 10rem;
    &::before{
      content: '';
      display: block;
      position: absolute;
      left: -32rem;
      top: -18rem;
      width: 20rem;
      height: 45rem;
      background: url('../img/arrow_1.png') no-repeat center / contain;
    }
    @media screen and (max-width: 991px) {
      bottom: -67rem;
    }
  }
}