.franchise {
	position: relative;
	margin-top: 100rem;
	&::before{
		content: '';
		display: inline-block;
		position: absolute;
		top: -356rem;
		left: 341rem;
		width: 1319rem;
		height: 1151rem;
		background: url('../img/franchise_image.png') no-repeat center / contain;
		@media screen and (max-width: 767px) {
			display: none;
		}
	}
	@media screen and (max-width: 767px) {
		margin-top: 69rem;
	}
	@include block-title(570rem, 595rem);
	@media screen and (min-width: 768px) {
		&__title {
			position: relative;
			margin: 0;
			text-align: left;
		}
		&__subtitle {
			position: relative;
			margin: 25rem 0 0;
			text-align: left;
		}
	}
	&__tile {
		position: relative;
		border-radius: 10rem;
		background-color: #232d60;
		max-width: 420rem;
		margin-top: 55rem;
		padding: 38rem 50rem 54rem;
		@media screen and (max-width: 767px) {
			margin-top: 35rem;
			padding: 47rem 29rem 54rem;
		}
		@media screen and (max-width: 360px) {
			padding: 47rem 15rem 54rem;
		}
		&-title {
			font-size: 24rem;
			font-weight: 700;
			letter-spacing: 0.48rem;
			color: var(--light-color);
			@media screen and (max-width: 767px) {
				font-size: 20rem;
			}
		}
	}
	&__numbers {
		&-item {
			display: flex;
			align-items: center;
			margin-top: 21rem;
			&:nth-child(1){
				.franchise__icon{
					min-width: 37rem;
				}
			}
			&:nth-child(2){
				.franchise__icon{
					min-width: 39rem;
				}
			}
			&:nth-child(3){
				.franchise__icon{
					min-width: 45rem;
				}
			}
		}
		&-wrapper {
			margin-left: 22rem;
		}
		&-text {
			font-size: 16rem;
			letter-spacing: 0.32rem;
			color: var(--light-color);
			display: inline-block;
			margin-top: 6rem;
		}
	}
	&__number {
		font-size: 24rem;
		font-weight: 700;
		letter-spacing: 0.48rem;
		color: var(--light-color);
	}
	&__button {
		margin-top: 40rem;
		@include button(23rem 28rem 21rem, 2rem);
		@media screen and (max-width: 767px) {
			width: 100%;
			padding: 24rem 3rem 21rem;
		}
	}
}