.about {
	margin-top: 100rem;
	@media screen and (max-width: 767px) {
		margin-top: 69rem;
	}
	@include block-title(720rem, 520rem);
	&__row {
		display: flex;
		justify-content: space-between;
		margin-top: 55rem;
		@media screen and (max-width: 991px) {
			flex-wrap: wrap;
			justify-content: center;
		}
	}
	&__column {
		display: flex;
		align-items: flex-start;
		flex-wrap: wrap;
		position: relative;
		@media screen and (max-width: 991px) {
			justify-content: center;
		}
	}
	&__tile {
		border-radius: 10rem;
		box-shadow: 0px 5px 40px 0 rgba(0, 0, 0, 0.05);
		background-color: #232d60;
		max-width: 520rem;
		padding: 27rem 52rem 50rem 54rem;
		@media screen and (max-width: 767px) {
			padding: 29rem;
			margin-top: 23rem;
		}
		&-heading {
			display: flex;
			justify-content: space-between;
			align-items: center;
			font-size: 22rem;
			font-weight: 500;
			letter-spacing: 0.44rem;
			text-align: center;
			color: var(--light-color);
		}
		&-logo{
			width: 78rem;
			@media screen and (max-width: 767px) {
				display: none;
			}
		}
		&-text {
			font-size: 16rem;
			letter-spacing: 0.32rem;
			color: rgba(255, 255, 255, 0.6);
			margin-top: 6rem;
			@media screen and (max-width: 767px) {
				margin-top: 29rem;
			}
		}
	}
	&__director {
		position: relative;
		display: flex;
		flex-direction: column;
		align-items: center;
		margin-left: 30rem;
		@media screen and (max-width: 991px) {
			margin-top: 25rem;
			margin-left: 0;
		}
		&-image {
			width: 244rem;
		}
		&-play{
			width: 120rem;
		}
		&-info {
			width: 320rem;
			text-align: center;
			border-radius: 10rem;
			box-shadow: 0 5rem 40rem 0 rgba(0, 0, 0, 0.05);
			background-color: #fff;
			margin-top: -48rem;
			padding: 14rem 5rem;
		}
		&-position {
			font-size: 16rem;
			letter-spacing: 0.32rem;
			color: var(--gray-color);
		}
		&-name {
			font-size: 18rem;
			font-weight: 700;
			letter-spacing: 0.36rem;
			display: inline-block;
			margin-top: 3rem;
		}
		&-button {
			display: flex;
			align-items: center;
			position: absolute;
			top: 13rem;
			right: -95rem;
			@media screen and (max-width: 991px) {
				position: relative;
				top: auto;
				right: auto;
				margin-top: 9rem;
			}
			&-text {
				font-size: 14rem;
				line-height: 1.57;
				letter-spacing: 0.28rem;
				text-transform: uppercase;
				text-decoration: underline;
				text-align: left;
				max-width: 105rem;
				margin-left: 13rem;
			}
		}
	}
	&__quote {
		position: absolute;
		top: 244rem;
		font-size: 20rem;
		letter-spacing: 0.4rem;
		border-left: 1px solid #ffd01c;
		max-width: 427rem;
		margin-left: 74rem;
		padding-left: 31rem;
		@media screen and (max-width: 991px) {
			position: relative;
			top: auto;
			font-size: 16rem;
			margin-top: 29rem;
		}
		@media screen and (max-width: 767px) {
			padding-left: 20rem;
			margin-left: 49rem;
		}
		&::before{
			content: '';
			display: inline-block;
			position: absolute;
			top: 50%;
			left: -74rem;
			transform: translateY(-50%);
			width: 50rem;
			height: 46rem;
			background: url('../img/quote.png') no-repeat center / cover;
			@media screen and (max-width: 767px) {
				width: 32rem;
				height: 30rem;
				left: -49rem;
			}
		}
		span{
			font-weight: 700;
		}
	}
	&__numbers {
		max-width: 188rem;
		margin-top: 4rem;
		@media screen and (max-width: 991px) {
			display: flex;
			justify-content: center;
			flex-wrap: wrap;
			max-width: 360rem;
			margin-top: 36rem;
		}
		@media screen and (max-width: 360px) {
			flex-direction: column;
		}
		&-item {
			&:nth-child(n+2){
				margin-top: 27rem;
			}
			@media screen and (max-width: 991px) {
				flex: 0 0 170rem;
				&:nth-child(n+2){
					margin-top: 0;
				}
				&:nth-child(n+3){
					margin-top: 35rem;
				}
				&:nth-child(even){
					margin-left: 15rem;
				}
			}
			@media screen and (max-width: 767px) {
				flex: 0 0 160rem;
				&:nth-child(even){
					margin-left: 5rem;
				}
			}
			@media screen and (max-width: 360px) {
				max-width: 160rem;
				flex: 0 0 auto;
				&:nth-child(n+2){
					margin-top: 20rem;
				}
				&:nth-child(even){
					margin-left: 0;
				}
			}
		}
		&-text {
			font-size: 18rem;
			letter-spacing: 0.36rem;
			color: #010101;
			margin-top: 7rem;
			@media screen and (max-width: 991px) {
				font-size: 16rem;
			}
			@media screen and (max-width: 767px) {
				font-size: 15rem;
			}
		}
		&-image{
			width: 26rem;
			margin-right: 18rem;
		}
	}
	&__number {
		display: flex;
		align-items: center;
		font-size: 32rem;
		font-weight: 700;
		color: #010101;
	}
}