.steps{
	margin-top: 100rem;
	@media screen and (max-width: 767px) {
		margin-top: 66rem;
	}
	@include block-title(670rem, 845rem);
	&__slider {
		margin-top: 50rem;
		overflow: visible;
		@media screen and (max-width: 767px) {
			margin-top: 38rem;
		}
		&-buttons {
			display: flex;
			justify-content: center;
			margin-top: 35rem;
		}
		&-prev {
			font-size: 18rem;
			font-weight: 700;
			letter-spacing: 0.36rem;
			color: rgba(1, 1, 1, 0.2);
			text-transform: uppercase;
			display: flex;
			align-items: center;
			padding: 18rem 32rem 14rem;
			border-radius: 3rem;
			box-shadow: 0 3rem 0 0 #e9e9e9;
			background-color: #f0f0f0;
			.steps__slider-arrow{
				margin-right: 9rem;
			}
		}
		&-next {
			font-size: 18rem;
			font-weight: 700;
			letter-spacing: 0.36rem;
			color: #010101;
			text-transform: uppercase;
			display: flex;
			align-items: center;
			padding: 18rem 32rem 14rem 31rem;
			border-radius: 3rem;
			box-shadow: 0 3rem 0 0 #e3bd2b;
			background-image: linear-gradient(to top, #f3c92a, #f4db7d);
			margin-left: 20rem;
			.steps__slider-arrow{
				margin-left: 9rem;
			}
			@media screen and (max-width: 767px) {
				margin-left: 10rem;
			}
		}
		.swiper-slide {
			opacity: 0.5;
			transition: 0.5s;
		}
		.swiper-slide-active, .swiper-slide-active + .swiper-slide, .swiper-slide-active + .swiper-slide + .swiper-slide, .swiper-slide-active + .swiper-slide + .swiper-slide + .swiper-slide {
			opacity: 1;
		}
		@media screen and (max-width: 991px) {
			.swiper-slide-active + .swiper-slide + .swiper-slide, .swiper-slide-active + .swiper-slide + .swiper-slide + .swiper-slide{
				opacity: 0.5;
			}
		}
		@media screen and (max-width: 767px) {
			.swiper-slide-active + .swiper-slide{
				opacity: 0.5;
			}
		}
	}
	&__item {
		text-align: center;
		border-radius: 10rem;
		box-shadow: 0 5rem 40rem 0 rgba(0, 0, 0, 0.05);
		background-color: var(--light-color);
		padding: 49rem 23rem 51rem;
		&:nth-child(odd){
			.steps__item-number{
				color: #232d60;
			}
		}
		&:nth-child(even){
			.steps__item-number{
				color: #ffd01c;
			}
		}
		&-number {
			font-size: 84rem;
			font-weight: 700;
			margin: 0 8rem;
			&-wrapper {
				font-size: 22rem;
				font-weight: 700;
				letter-spacing: 0.44rem;
				color: rgba(0, 0, 0, 0.3);
			}
		}
		&-title {
			font-size: 20rem;
			font-weight: 700;
			letter-spacing: 0.4rem;
			display: inline-block;
			white-space: nowrap;
			margin-top: 10rem;
		}
		&-subtitle {
			font-size: 16rem;
			letter-spacing: 0.32rem;
			color: var(--gray-color);
			margin-top: 16rem;
		}
	}
}