.tools {
	margin-top: 100rem;
	@media screen and (max-width: 767px) {
		margin-top: 70rem;
	}
	@include block-title(1035rem, 825rem);
	&__row {
		display: flex;
		justify-content: space-between;
		flex-wrap: wrap;
		margin-top: 54rem;
		@media screen and (max-width: 767px) {
			margin-top: 36rem;
		}
	}
	&__item {
		flex: 0 0 calc(33.333% - 10rem);
		border-radius: 10rem;
		box-shadow: 0rem 5rem 40rem 0 rgba(0, 0, 0, 0.05);
		background-color: var(--light-color);
		padding: 53rem 51rem 54rem;
		text-align: center;
		@media screen and (max-width: 991px) {
			flex: 0 0 calc(50% - 5rem);
			&:nth-child(n+3){
				margin-top: 12rem;
			}
		}
		@media screen and (max-width: 991px) {
			flex: 0 0 100%;
			padding: 50rem 50rem 57rem;
			&:nth-child(n+2){
				margin-top: 12rem;
			}
		}
		&:nth-child(odd){
			.tools__item-image-wrapper{
				background-color: #ffd01c;
			}
		}
		&:nth-child(even){
			.tools__item-image-wrapper{
				background-color: #232d60;
			}
		}
		&:nth-child(n+4){
			margin-top: 15rem;
			@media screen and (max-width: 991px) {
				margin-top: 12rem;
			}
		}
		&-image {
			&-wrapper {
				display: flex;
				justify-content: center;
				align-items: center;
				border-radius: 50%;
				width: 80rem;
				height: 80rem;
				margin: 0 auto;
			}
		}
		&-title {
			font-size: 20rem;
			font-weight: 700;
			letter-spacing: 0.4rem;
			display: inline-block;
			margin-top: 20rem;
		}
		&-subtitle {
			font-size: 16rem;
			letter-spacing: 0.32rem;
			color: var(--gray-color);
			max-width: 277rem;
			margin: 13rem auto 0;
			@media screen and (max-width: 767px) {
				max-width: 100%;
			}
		}
	}
}