.footer {
	background-color: #232d60;
	padding: 51rem 0 56rem;
	margin-top: 100rem;
	@media screen and (max-width: 991px) {
		padding: 42rem 0 47rem;
		margin-top: 59rem;
	}
	&__container{
		display: flex;
		align-items: center;
		justify-content: space-between;
		@media screen and (max-width: 991px) {
			flex-direction: column-reverse;
			text-align: center;
		}
	}
	&__logo{
		@media screen and (max-width: 991px) {
			display: none;
		}
	}
	&__company {
		display: flex;
		align-items: center;
		&-column {
			margin-left: 43px;
			@media screen and (max-width: 991px) {
				margin: 29rem 0 0;
			}
		}
	}
	&__description {
		font-size: 18rem;
		letter-spacing: 0.36rem;
		color: var(--light-color);
		max-width: 290rem;
	}
	&__copyrights {
		font-size: 14rem;
		letter-spacing: 0.28rem;
		color: var(--light-color);
		margin-top: 23rem;
	}
	&__confidentiality {
		font-size: 14rem;
		letter-spacing: 0.28rem;
		color: rgba(255, 255, 255, 0.6);
		border-bottom: 1px solid rgba(255, 255, 255, 0.6);
		display: inline-block;
		margin-top: 12rem;
	}
	&__owner {
		font-size: 16rem;
		letter-spacing: 0.32rem;
		color: rgba(255, 255, 255, 0.6);
	}
	&__address {
		font-size: 16rem;
		letter-spacing: 0.32rem;
		color: var(--light-color);
		max-width: 195rem;
		margin-top: 18rem;
		span{
			font-weight: 700;
		}
	}
	&__info{
		@media screen and (max-width: 991px) {
			margin-top: 37rem;
		}
	}
	&__contacts {
		@media screen and (max-width: 991px) {
			display: flex;
			flex-direction: column;
			align-items: center;
			margin-left: 8rem;
		}
		&-item {
			position: relative;
			display: flex;
			align-items: center;
			font-size: 16rem;
			letter-spacing: 0.32rem;
			color: var(--light-color);
			&::before{
				content: '';
				display: inline-block;
				position: absolute;
				top: 50%;
				transform: translateY(-50%);
			}
			&:nth-child(1){
				&::before{
					left: -29rem;
					width: 17rem;
					height: 12rem;
					background: url('../img/mail.png') no-repeat center / cover;
				}
			}
			&:nth-child(2){
				&::before{
					left: -28rem;
					width: 15rem;
					height: 16rem;
					background: url('../img/phone.png') no-repeat center / cover;
				}
			}
			&:nth-child(3){
				&::before{
					left: -27rem;
					width: 15rem;
					height: 15rem;
					background: url('../img/callback.png') no-repeat center / cover;
				}
			}
			&:nth-child(4){
				&::before{
					left: -27rem;
					width: 17rem;
					height: 17rem;
					background: url('../img/website.png') no-repeat center / cover;
				}
			}
			&:nth-child(n+2){
				margin-top: 13rem;
			}
		}
		&-link {
			font-size: 16rem;
			letter-spacing: 0.32rem;
			color: var(--light-color);
		}
		&-callback {
			font-size: 16rem;
			font-weight: 700;
			letter-spacing: 0.32rem;
			color: var(--light-color);
			border-bottom: 1px solid var(--light-color);
		}
		&-website{
			font-size: 16rem;
			letter-spacing: 0.32rem;
			color: var(--light-color);
			font-weight: 700;
			margin-left: 4rem;
		}
	}
	&__messengers {
		display: flex;
		align-items: center;
		margin-left: 10rem;
		&-item {
			&:nth-child(n+2){
				margin-left: 5rem;
			}
		}
		&-icon{
			width: 16rem;
			height: 16rem;
		}
	}
}