.partnership {
	margin-top: 86rem;
	@media screen and (max-width: 767px) {
		margin-top: 67rem;
	}
	@include block-title(890rem, 885rem);
	&__row {
		position: relative;
		display: flex;
		justify-content: space-between;
		margin-top: 54rem;
		&::before{
			content: '';
			display: inline-block;
			position: absolute;
			top: 127rem;
			right: 568rem;
			width: 1149rem;
			height: 335rem;
			background: url('../img/pattern.png') no-repeat center / contain;
			@media screen and (max-width: 767px) {
				top: 126rem;
				right: 63rem;
			}
		}
		@media screen and (max-width: 991px) {
			flex-wrap: wrap;
		}
	}
	&__problems {
		position: relative;
		border-radius: 10rem;
		box-shadow: 0 5rem 40rem 0 rgba(0, 0, 0, 0.05);
		background-color: var(--light-color);
		max-width: 472rem;
		padding: 42rem 76rem 49rem 55rem;
		@media screen and (max-width: 991px) {
			padding: 32rem 25rem 39rem 25rem;
		}
		&-wrapper{
			position: relative;
			&::before{
				content: '';
				display: inline-block;
				position: absolute;
				top: 165rem;
				left: 182rem;
				width: 502rem;
				height: 346rem;
				background: url('../img/partnership_image.png') no-repeat center / contain;
				@media screen and (max-width: 991px) {
					display: none;
				}
				@media screen and (max-width: 767px) {
					width: 287rem;
					height: 198rem;
					top: auto;
					bottom: -67rem;
					left: 51rem;
					display: inline-block;
				}
			}
		}
		&-title {
			font-size: 20rem;
			font-weight: 700;
			letter-spacing: 0.4rem;
			display: flex;
			align-items: center;
			&-text{
				max-width: 232rem;
			}
		}
		&-warning{
			min-width: 44rem;
			width: 44rem;
			height: 44rem;
			margin-right: 19rem;
		}
		&-list {
			font-size: 16rem;
			letter-spacing: 0.32rem;
			margin-top: 25rem;
			@media screen and (max-width: 991px) {
				font-size: 15rem;
				letter-spacing: 0.3rem;
				margin-top: 18rem;
			}
		}
		&-item {
			&:nth-child(n+2){
				margin-top: 9rem;
				@media screen and (max-width: 991px) {
					margin-top: 8rem;
				}
			}
		}
	}
	&__helpfulness {
		border-radius: 10rem;
		background-color: #232d60;
		max-width: 544rem;
		width: 544rem;
		padding: 45rem 70rem 45rem;
		margin-top: 71rem;
		@media screen and (max-width: 991px) {
			padding: 43rem 24rem 37rem 45rem;
			width: 100%;
			max-width: 340rem;
		}
		&-wrapper{
			position: relative;
			@media screen and (max-width: 991px) {
				margin-top: 77rem;
			}
		}
		&-title{
			font-size: 28rem;
			font-weight: 700;
			letter-spacing: 0.56rem;
			position: absolute;
			top: 3rem;
			left: -139rem;
			&::before{
				content: '';
				display: inline-block;
				position: absolute;
				top: 55rem;
				left: 29rem;
				width: 74rem;
				height: 67rem;
				background: url('../img/partnership_arrow.png') no-repeat center / contain;
				@media screen and (max-width: 991px) {
					display: none;
				}
			}
			@media screen and (max-width: 991px) {
				left: 0;
				top: 10rem;
			}
			@media screen and (max-width: 360rem) {
				font-size: 26rem;
			}
		}
		&-item {
			display: flex;
			align-items: center;
			position: relative;
			&:nth-child(n+2){
				margin-top: 25rem;
			}
			&::before{
				content: '';
				display: inline-block;
				position: absolute;
				top: 50%;
				left: -30rem;
				transform: translateY(-50%);
				width: 38rem;
				height: 37rem;
				background: url('../img/partnership_check.png') no-repeat center / contain;
				@media screen and (max-width: 991px) {
					width: 29rem;
					height: 29rem;
					left: -15rem;
				}
			}
		}
		&-icon {
			width: 50rem;
			height: 50rem;
			position: relative;
			@media screen and (max-width: 991px) {
				min-width: 39rem;
				width: 39rem;
				height: 39rem;
			}
		}
		&-text{
			font-size: 18rem;
			letter-spacing: 0.36rem;
			color: var(--light-color);
			max-width: 330rem;
			margin-left: 20rem;
			@media screen and (max-width: 991px) {
				max-width: 214rem;
				margin-left: 15rem;
				font-size: 16rem;
				letter-spacing: 0.32rem;
			}
		}
	}
}