.main{
  display: flex;
  justify-content: space-between;
  padding-top: 76rem;
  padding-bottom: 130rem;
  position: relative;
  @media screen and (max-width: 991px) {
    flex-wrap: wrap;
    padding-top: 44rem;
  }
  &::before{
    content: '';
    display: block;
    width: 267rem;
    height: 295rem;
    background: url('../img/main_pattern.png') no-repeat center / cover;
    position: absolute;
    top: -80rem;
    left: -247rem;
    z-index: 0;
    @media screen and (max-width: 991px) {
      top: -120rem;
      left: -287rem;
    }
    @media screen and (max-width: 767px) {
      top: -60rem;
      left: -287rem;
    }
  }
  &::after{
    content: '';
    display: block;
    width: 250rem;
    height: 359rem;
    background: url('../img/main_image.png') no-repeat center / cover;
    position: absolute;
    top: 43rem;
    left: -260rem;
    @media screen and (max-width: 767px) {
      top: 74rem;
      left: -224rem;
    }
  }
  &__column {
    z-index: 2;
    &:nth-child(1){
      max-width: 640rem;
      @media screen and (max-width: 991px) {
        max-width: 100%;
      }
    }
    &:nth-child(2){
      position: relative;
      &::before{
        content: '';
        display: block;
        width: 1028rem;
        height: 476rem;
        background: url('../img/main_bg.png') no-repeat center / cover;
        position: absolute;
        top: 72rem;
        left: -374rem;
        z-index: 0;
      }
      @media screen and (max-width: 991px) {
        margin-top: 89rem;
        &::before{
          content: '';
          display: block;
          width: 468rem;
          height: 544rem;
          background: url('../img/main_bg_1.png') no-repeat center / cover;
          position: absolute;
          top: 20rem;
          left: auto;
          right: -60vw;
          z-index: 0;
        }
      }
      @media screen and (max-width: 767px) {
        &::before{
          right: -260rem;
        }
      }
    }
  }
  &__title {
    font-size: 36rem;
    font-weight: bold;
    letter-spacing: 0.72rem;
    @media screen and (max-width: 991px) {
      font-size: 29rem;
      letter-spacing: 0.58px;
    }
  }
  &__subtitle {
    font-size: 20rem;
    letter-spacing: 0.4rem;
    max-width: 467rem;
    margin-top: 35rem;
    @media screen and (max-width: 991px) {
      font-size: 18rem;
      margin-top: 27rem;
    }
  }
  &__button {
    @include button(21rem 65rem, 2rem);
    margin-top: 50rem;
    @media screen and (max-width: 991px) {
      padding-left: 39rem;
      padding-right: 39rem;
      width: 340rem;
      max-width: 100%;
    }
    &-text {
      color: var(--gray-color);
      font-size: 14rem;
      letter-spacing: 0.28rem;
      max-width: 251rem;
      margin-left: 13rem;
      &-wrapper {
        display: flex;
        align-items: center;
        position: relative;
        top: 69rem;
        left: 228rem;
        &::before{
          content: '';
          display: block;
          width: 49rem;
          height: 54rem;
          background: url('../img/arrow.png') no-repeat center / cover;
          position: absolute;
          top: -36rem;
          left: -70rem;
          z-index: 0;
        }
        @media screen and (max-width: 991px) {
          top: 34rem;
          left: 20rem;
          &::before{
            display: none;
          }
        }
      }
    }
    &-image {
      width: 28rem;
      height: auto;
    }
  }
  &__tile {
    border-radius: 10rem;
    box-shadow: 0 10rem 16rem 4rem rgba(0, 0, 0, 0.1);
    background-color: #232d60;
    width: 293rem;
    height: 471rem;
    background: url('../img/main_tile.png') no-repeat center / cover;;
    margin-top: 11rem;
    padding: 53rem 50rem 46rem;
    position: relative;
    @media screen and (max-width: 991px) {
      margin-top: 0;
    }
  }
}
.tile{
  &__title {
    font-size: 24rem;
    font-weight: bold;
    letter-spacing: 0.48rem;
    color: var(--light-color);
    span{
      color: #ffd01c;
      display: block;
    }
  }
  &__list {
    margin-top: 56rem;
    &-item {
      &:nth-child(n+2){
        margin-top: 40rem;
      }
    }
    &-image {
      width: 56rem;
      height: 56rem;
    }
    &-text {
      color: var(--light-color);
      letter-spacing: 0.32rem;
      margin-top: 15rem;
      max-width: 165rem;
    }
  }
}