.pictures {
	margin-top: 87rem;
	@media screen and (max-width: 767px) {
		margin-top: 34rem;
	}
	&__title {
		display: flex;
		justify-content: center;
		align-items: center;
		font-size: 32rem;
		font-weight: 700;
		letter-spacing: 0.64rem;
		text-align: left;
		color: #000;
		&-icon {
			margin-right: 18rem;
		}
    @media screen and (max-width: 991px) {
      font-size: 28rem;
    }
    @media screen and (max-width: 767px) {
    	flex-direction: column;
      font-size: 22rem;
      letter-spacing: 0.4rem;
			&-icon {
				margin-right: 0;
				margin-top: 6rem;
			}
    }
	}
	&__slider {
		margin-top: 50rem;
		overflow: visible;
		@media screen and (max-width: 767px) {
			overflow: hidden;
			margin-top: 23rem;
		}
		&-buttons {
			display: flex;
			justify-content: center;
			margin-top: 35rem;
			@media screen and (max-width: 767px) {
				margin-top: 20rem;
			}
		}
		&-prev {
			font-size: 18rem;
			font-weight: 700;
			letter-spacing: 0.36rem;
			color: rgba(1, 1, 1, 0.2);
			text-transform: uppercase;
			display: flex;
			align-items: center;
			padding: 18rem 32rem 14rem;
			border-radius: 3rem;
			box-shadow: 0 3rem 0 0 #e9e9e9;
			background-color: #f0f0f0;
			.pictures__slider-arrow{
				margin-right: 9rem;
			}
		}
		&-next {
			font-size: 18rem;
			font-weight: 700;
			letter-spacing: 0.36rem;
			color: #010101;
			text-transform: uppercase;
			display: flex;
			align-items: center;
			padding: 18rem 32rem 14rem 31rem;
			border-radius: 3rem;
			box-shadow: 0 3rem 0 0 #e3bd2b;
			background-image: linear-gradient(to top, #f3c92a, #f4db7d);
			margin-left: 20rem;
			.pictures__slider-arrow{
				margin-left: 9rem;
			}
			@media screen and (max-width: 767px) {
				margin-left: 10rem;
			}
		}
		.swiper-slide {
			opacity: 0.5;
			transition: 0.5s;
		}
		.swiper-slide-active, .swiper-slide-active + .swiper-slide, .swiper-slide-active + .swiper-slide + .swiper-slide {
			opacity: 1;
		}
		@media screen and (max-width: 991px) {
			.swiper-slide-active + .swiper-slide + .swiper-slide{
				opacity: 0.5;
			}
		}
		@media screen and (max-width: 767px) {
			.swiper-slide-active + .swiper-slide{
				opacity: 0.5;
			}
		}
	}
	&__item {
		border-radius: 10px;
		box-shadow: 0px 5px 40px 0 rgba(0, 0, 0, 0.05);
		background-color: #232d60;
	}
}